<template>
  <div class="home">

    <el-row>
      <el-col :span="12" style="min-height: 1000px;">
        <div v-for="i in 400" :key="i" :id="'aaa' + i" style="height: 50px;
                display: block;
                border-bottom: 1px solid #eee;">{{i}}</div>
        <div id="element" v-scroll-to="'#logo'">
          Hi I'm #element
        </div>
      </el-col>
      <el-col :span="12" style="min-height: 1000px;">
        <div class="grid-content bg-purple-light"></div>
      </el-col>
    </el-row>

  </div>
</template>
  
<script>

// import contactList from '../data/history.json'
export default {
  name: 'home',
  mounted() {
    this.$scrollTo('#aaa60', 1000, { easing: 'ease', cancelable: false })
  },
  watch: {
    '$route.path': function (to, from) {
      console.log(to.split('/')[1]);
    }
  },

  data() {
    return {
      contactList: {}
    }
  },
  components: {
    // homepage
  }
}
</script>
  


